"use client"

import { useEffect, useState } from "react"
import { dataStore } from "@/lib/data-store"
import { League } from "@/lib/types"

export default function AdminLeagues() {
  const [leagues, setLeagues] = useState<League[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [filter, setFilter] = useState<'all' | 'open' | 'closed' | 'in-progress' | 'completed'>('all')

  useEffect(() => {
    try {
      const allLeagues = dataStore.getAllLeagues()
      setLeagues(allLeagues)
    } catch (err) {
      setError("Failed to load leagues")
      console.error("Leagues error:", err)
    } finally {
      setLoading(false)
    }
  }, [])

  const filteredLeagues = leagues.filter(league => {
    if (filter === 'all') return true
    return league.status === filter
  })

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat('en-US', {
      style: 'currency',
      currency: 'USD'
    }).format(amount)
  }

  const formatDate = (date: Date) => {
    return new Date(date).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'short',
      day: 'numeric'
    })
  }

  const getStatusBadge = (status: string) => {
    const baseClasses = "px-2 py-1 text-xs font-medium rounded-full"
    switch (status) {
      case 'open':
        return `${baseClasses} bg-green-100 text-green-800`
      case 'closed':
        return `${baseClasses} bg-red-100 text-red-800`
      case 'in-progress':
        return `${baseClasses} bg-blue-100 text-blue-800`
      case 'completed':
        return `${baseClasses} bg-gray-100 text-gray-800`
      default:
        return `${baseClasses} bg-gray-100 text-gray-800`
    }
  }

  const getDayName = (dayOfWeek: number) => {
    const days = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday']
    return days[dayOfWeek] || 'Unknown'
  }

  if (loading) {
    return (
      <div className="flex items-center justify-center h-64">
        <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-gray-900"></div>
      </div>
    )
  }

  if (error) {
    return (
      <div className="bg-red-50 border border-red-200 rounded-md p-4">
        <p className="text-red-800">{error}</p>
      </div>
    )
  }

  return (
    <div>
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-gray-900">League Management</h1>
        <p className="text-gray-600 mt-2">Organize and manage golf leagues</p>
      </div>

      {/* Filter Tabs */}
      <div className="mb-6">
        <div className="border-b border-gray-200">
          <nav className="-mb-px flex space-x-8">
            {[
              { key: 'all', label: 'All Leagues', count: leagues.length },
              { key: 'open', label: 'Open', count: leagues.filter(l => l.status === 'open').length },
              { key: 'in-progress', label: 'In Progress', count: leagues.filter(l => l.status === 'in-progress').length },
              { key: 'completed', label: 'Completed', count: leagues.filter(l => l.status === 'completed').length },
              { key: 'closed', label: 'Closed', count: leagues.filter(l => l.status === 'closed').length },
            ].map((tab) => (
              <button
                key={tab.key}
                onClick={() => setFilter(tab.key as any)}
                className={`py-2 px-1 border-b-2 font-medium text-sm ${
                  filter === tab.key
                    ? 'border-gray-900 text-gray-900'
                    : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                }`}
              >
                {tab.label} ({tab.count})
              </button>
            ))}
          </nav>
        </div>
      </div>

      {/* Add League Button */}
      <div className="mb-6">
        <button className="bg-gray-900 text-white px-4 py-2 rounded-md hover:bg-gray-800 transition-colors">
          Create New League
        </button>
      </div>

      {/* Leagues Grid */}
      {filteredLeagues.length === 0 ? (
        <div className="bg-white rounded-lg shadow p-8 text-center">
          <p className="text-gray-500">No leagues found for the selected filter.</p>
        </div>
      ) : (
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
          {filteredLeagues.map((league) => (
            <div key={league.id} className="bg-white rounded-lg shadow p-6">
              <div className="flex justify-between items-start mb-4">
                <div>
                  <h3 className="text-lg font-semibold text-gray-900">{league.name}</h3>
                  <p className="text-sm text-gray-600 mt-1">{league.description}</p>
                </div>
                <span className={getStatusBadge(league.status)}>
                  {league.status}
                </span>
              </div>

              <div className="grid grid-cols-2 gap-4 mb-4">
                <div>
                  <p className="text-xs font-medium text-gray-500 uppercase tracking-wide">Format</p>
                  <p className="text-sm text-gray-900 capitalize">{league.format}</p>
                </div>
                <div>
                  <p className="text-xs font-medium text-gray-500 uppercase tracking-wide">Match Type</p>
                  <p className="text-sm text-gray-900 capitalize">{league.matchType.replace('-', ' ')}</p>
                </div>
                <div>
                  <p className="text-xs font-medium text-gray-500 uppercase tracking-wide">Price per Person</p>
                  <p className="text-sm text-gray-900">{formatCurrency(league.pricePerPerson)}</p>
                </div>
                <div>
                  <p className="text-xs font-medium text-gray-500 uppercase tracking-wide">Max Teams</p>
                  <p className="text-sm text-gray-900">{league.maxTeams}</p>
                </div>
              </div>

              <div className="grid grid-cols-1 gap-4 mb-4">
                <div>
                  <p className="text-xs font-medium text-gray-500 uppercase tracking-wide">Schedule</p>
                  <p className="text-sm text-gray-900">
                    {getDayName(league.dayOfWeek)}s at {league.startTime}
                  </p>
                  <p className="text-xs text-gray-600">
                    {formatDate(league.startDate)} - {formatDate(league.endDate)} ({league.numberOfWeeks} weeks)
                  </p>
                </div>
                <div>
                  <p className="text-xs font-medium text-gray-500 uppercase tracking-wide">Bay Allocation</p>
                  <p className="text-sm text-gray-900">
                    Bays: {league.bayAllocation.join(', ')}
                  </p>
                </div>
              </div>

              <div className="flex justify-between items-center pt-4 border-t border-gray-200">
                <div className="text-xs text-gray-500">
                  Created {formatDate(league.createdAt)}
                </div>
                <div className="flex space-x-2">
                  <button className="text-sm text-gray-600 hover:text-gray-900 transition-colors">
                    View Details
                  </button>
                  <button className="text-sm text-gray-600 hover:text-gray-900 transition-colors">
                    Edit
                  </button>
                  {league.status === 'open' && (
                    <button className="text-sm text-red-600 hover:text-red-900 transition-colors">
                      Close Registration
                    </button>
                  )}
                </div>
              </div>
            </div>
          ))}
        </div>
      )}

      {/* Summary Stats */}
      <div className="mt-8 grid grid-cols-1 md:grid-cols-4 gap-4">
        <div className="bg-white rounded-lg shadow p-4">
          <h3 className="text-sm font-medium text-gray-500">Total Leagues</h3>
          <p className="text-2xl font-bold text-gray-900">{filteredLeagues.length}</p>
        </div>
        <div className="bg-white rounded-lg shadow p-4">
          <h3 className="text-sm font-medium text-gray-500">Active Leagues</h3>
          <p className="text-2xl font-bold text-blue-600">
            {leagues.filter(l => l.status === 'in-progress').length}
          </p>
        </div>
        <div className="bg-white rounded-lg shadow p-4">
          <h3 className="text-sm font-medium text-gray-500">Open for Registration</h3>
          <p className="text-2xl font-bold text-green-600">
            {leagues.filter(l => l.status === 'open').length}
          </p>
        </div>
        <div className="bg-white rounded-lg shadow p-4">
          <h3 className="text-sm font-medium text-gray-500">Potential Revenue</h3>
          <p className="text-2xl font-bold text-gray-900">
            {formatCurrency(
              filteredLeagues.reduce((sum, l) => sum + (l.pricePerPerson * l.maxTeams * (l.format === 'single' ? 1 : l.format === '2-man' ? 2 : 4)), 0)
            )}
          </p>
        </div>
      </div>
    </div>
  )
}
